import React, { useState, useEffect } from "react";
import { useParams } from "react-router-dom";
import { NavBar } from "../components";
import { Translate } from "../components";

import Title from '../components/Title'
import Text from '../components/Text'


function Food() {
  const { id } = useParams();

  const url = `https://api.nal.usda.gov/fdc/v1/food/${id}?api_key=CUctXInvQ21gAU3oW8YHYKHgBf7iBRwPZFSI7UcC`;

  const [data, setData] = useState([]);
  const fetchApi = async () => {
    const response = await fetch(url);
    console.log(response.status);
    const responseJson = await response.json();
    console.log(responseJson.foodNutrients);
    setData(responseJson.foodNutrients);
  };

  useEffect(() => {
    fetchApi();
  }, []);

  return (
    <div className="background-food">
      <NavBar />
    <div className="details-food">
      {data ? data.map((food) => (
         <div  className="element" key={food.nutrient.id}>
         <Title className="composition-title-food" value={food.nutrient.name}/>
         <Text className="composition-value-food" value={food.amount+food.nutrient.unitName}/> 
         </div>
      )):""
      }
      <Translate />
      </div>
    </div>
  );
}

export default Food;
