import React from 'react'
	
function Input({ type = 'text',name='',placeholder, onChange, value, maxLength = 30, extraClass = '' }) {
    return (
        <input
            type={type}
            name={name}
            onChange={(e) => onChange(e.target.value)}
            value={value}
            className={`input ${extraClass}`}
            maxLength={maxLength}
            placeholder={placeholder}
        />
    )
}
export default Input
