import { useState, useEffect } from "react";
import { FormattedMessage } from "react-intl";
import { Link } from "react-router-dom";
import { Input} from "./";

export default function SearchFood() {

  const url = "https://api.nal.usda.gov/fdc/v1/foods/list?api_key=CUctXInvQ21gAU3oW8YHYKHgBf7iBRwPZFSI7UcC&pageSize=200";
 
  const [foods, setFoods] = useState([]);
  const [search, setSearch] = useState("");

 
  const fetchApi = async () => {
    const response = await fetch(url);
    const responseJson = await response.json();
    setFoods(responseJson);
    console.log(responseJson);
  };
 
  useEffect(() => {
    fetchApi();
  }, []);


  return (
    <div className="content">
      <div className="searchFood">
        <FormattedMessage id="title.search" defaultMessage="Bedca">
          {(placeholder) => <h2>{placeholder}</h2>}
        </FormattedMessage>
        <FormattedMessage id="subTitle.search" defaultMessage="Write any food:">
          {(placeholder) => <h4>{placeholder}</h4>}
        </FormattedMessage>
        <div className="searchTool">
          <FormattedMessage
            id="input.search"
            defaultMessage="Write your search"
          >
            {(placeholder) => (
              <Input
                type="text"
                name="search"
                id="search"
                placeholder={placeholder}
                onChange={setSearch}
                extraClass="search"
              />
            )}
          </FormattedMessage>
          
        </div>
        <div className="list">
      
           {foods
            ? foods
                .filter((food) =>
                search.length > 0
                    ? food.description.toLowerCase().includes(search)
                    : ""
                )
                .map((food, key) => (
                  <div className="food" key={key}>
                    <Link to={`/food/${food.fdcId}`}>
                      <div className="id">
                        <span href={food.fdcId}>{food.description}</span>
                        
                      </div>
                    </Link>
                  </div>
                ))
            : "cargando"} 
      </div>
      </div>
    </div>
  );
}
